<template>
  <div v-show="is_show_page">
    <div class="sign">
      <div class="logo"><a href="/"><img src="/static/image/nav-logo.png" alt="Logo"></a></div>
      <div class="main">
        <h4 class="title">
          <div class="normal-title">
            <a :class="status===1?'active':''" @click="status=1"></a>
            <b></b>
            <a :class="status===2?'active':''" @click="status=2"></a>
          </div>
        </h4>
        <div class="js-sign-in-container" v-if="status===1">
          <form action="" method="post">
            <div class="input-prepend restyle js-normal">
              <input placeholder="" type="text" v-model="username">
              <i class="iconfont ic-user"></i>
            </div>
            <div class="input-prepend">
              <input placeholder="" type="password" v-model="password">
              <i class="iconfont ic-password"></i>
            </div>
            <div class="forget-btn">
              <router-link to="/find_password">?</router-link>
            </div>
            <button class="sign-in-button" type="button" @click.prevent="show_captcha">
              <span></span>
            </button>
          </form>
        </div>
        <div class="js-sign-in-container" v-if="status===2">
          <form class="new_user" id="new_user" action="" accept-charset="UTF-8" method="post">
            <div class="input-prepend restyle">
              <input placeholder="" type="text" value="" v-model="nickname" id="user_nickname">
              <i class="iconfont ic-user"></i>
            </div>
            <div class="input-prepend restyle no-radius js-normal">
              <input placeholder="" type="tel" v-model="mobile" id="user_mobile_number">
              <i class="iconfont ic-phonenumber"></i>
            </div>
            <div class="input-prepend restyle no-radius security-up-code js-security-number" v-if="is_show_sms_code">
              <input type="text" v-model="sms_code" id="sms_code" placeholder="">
              <i class="iconfont ic-verify"></i>
              <a tabindex="-1" class="btn-up-resend js-send-code-button" href="javascript:void(0);" id="send_code"
                 @click.prevent="send_sms">{{ sms_code_text }}</a>
            </div>
            <input type="hidden" name="security_number" id="security_number">
            <div class="input-prepend">
              <input placeholder="" type="password" v-model="password" id="user_password">
              <i class="iconfont ic-password"></i>
            </div>
            <input type="submit" name="commit" value="" class="sign-up-button" id="sign_up_btn"
                   @click.prevent="registerHandler">
            <p class="sign-up-msg">  <br> <a target="_blank" href=""></a>  <a target="_blank"
                                                                                                      href=""></a> 
            </p>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "QQCallBack",
  data() {
    return {
      is_show_page: false, // QQ,
      status: 1,  // QQ,1,2
      username: "",
      password: "",
      nickname: "",
      mobile: "",
      sms_code: "",
      sms_code_text: "",
      is_show_sms_code: false,
      openid: "", // QQopenid
      access_token: "", // QQaccess_token
      refresh_token: "", // QQrefresh_token
    }
  },
  watch: {
    mobile() {
      if (/^1[3-9]\d{9}$/.test(this.mobile)) {
        // ajax
        this.check_mobile();
      } else {
        this.is_show_sms_code = false;
      }
    }
  },
  created() {
    // QQcodestate
    //  query_string
    //   router params
    // let code = this.$route.query.code
    // let state = this.$route.query.state
    // console.log(code, state)
    this.get_user_info();
  },
  methods: {
    get_user_info() {
      // QQcodestate

      // code Authorization Code Access Token
      this.$axios.get(`${this.$settings.Host}/oauth/qq/user/`, {
        params: {
          code: this.$route.query.code,
          state: this.$route.query.state,
        }
      }).then(response => {
        //
        if (response.data.token) {
          // jwt
          sessionStorage.user_token = response.data.token;
          sessionStorage.user_id = response.data.id;
          sessionStorage.user_name = response.data.username;
          sessionStorage.user_avatar = response.data.avatar;
          localStorage.removeItem("user_token");
          localStorage.removeItem("user_id");
          localStorage.removeItem("user_name");
          localStorage.removeItem("user_avatar");

          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(() => {
            // 
            this.$router.push("/users");
          }).catch(() => {
            // 
            this.$router.push("/");
          });
        } else {
          // QQ,
          this.is_show_page = true;
          this.openid = response.data.openid;
          this.access_token = response.data.access_token;
          this.refresh_token = response.data.refresh_token;
        }
      }).catch(error => {
        let self = this;
        self.$message({
          type: "error",
          duration: 2000,
          message: "QQ!,....",
          onClose() {
            self.$router.push("/user/login");
          }
        });
      });
    },
    show_captcha() {
      // 

      // 
      if (this.username.length < 1 || this.password.length < 1) {
        // 
        return false;
      }


      var captcha1 = new TencentCaptcha(this.$settings.TC_captcha.app_id, res => {
        // 
        // res
        /**
         res:
         appid: "2086888489"  # APPID
         randstr: "@G0V"      # 
         ret: 0               # 02
         ticket: ""           # python
         */
        this.$axios.get(`${this.$settings.Host}/users/captcha/`, {
          params: {
            ticket: res.ticket,
            randstr: res.randstr
          }
        }).then(response => {
          if (response.data.detail) {
            // 
            if (this.status === 1) {
              this.loginhandler();
            } else {
              this.registerHandler();
            }
          }
        }).catch(error => {
          this.$message.error("");
        });
      });
      captcha1.show(); // 
    },
    loginhandler() {
      // QQ
      this.$axios.post(`${this.$settings.Host}/oauth/qq/login/`, {
        username: this.username,
        password: this.password,
        openid: this.openid,
        access_token: this.access_token,
        refresh_token: this.refresh_token,
      }).then(response => {
        if (response.data.token) {
          // jwt
          sessionStorage.user_token = response.data.token;
          sessionStorage.user_id = response.data.id;
          sessionStorage.user_name = response.data.username;
          sessionStorage.user_avatar = response.data.avatar;
          localStorage.removeItem("user_token");
          localStorage.removeItem("user_id");
          localStorage.removeItem("user_name");
          localStorage.removeItem("user_avatar");

          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(() => {
            // 
            this.$router.push("/users");
          }).catch(() => {
            // 
            this.$router.push("/");
          });
        } else {
          // QQ,
          this.is_show_page = true;
          this.openid = response.data.openid;
          this.access_token = response.data.access_token;
          this.refresh_token = response.data.refresh_token;
        }
      }).catch(error => {
        let self = this;
        self.$message({
          type: "error",
          duration: 2000,
          message: "QQ!,....",
          onClose() {
            self.$router.push("/user/login");
          }
        });
      })
    },
    send_sms() {
      // 
      if (this.mobile.length < 1) {
        this.$message.error(",!");
        return false;
      }

      this.$axios.get(`${this.$settings.Host}/users/sms/${this.mobile}/`).then(response => {
        this.$message.info(response.data.detail);
      }).catch(error => {
        this.$message.error("!!");
      });
    },
    check_mobile() {
      // 
      this.$axios.get(`${this.$settings.Host}/users/mobile/${this.mobile}/`).then(response => {
        console.log(response.data);
        this.is_show_sms_code = true;  // 
      }).catch(error => {
        this.$message.error(",!");
        this.is_show_sms_code = false; // 
      })
    },
    registerHandler() {
      // QQ
      this.$axios.post(`${this.$settings.Host}/oauth/qq/reg/`, {
        mobile: this.mobile,
        nickname: this.nickname,
        sms_code: this.sms_code,
        password: this.password,
        openid: this.openid,
        access_token: this.access_token,
        refresh_token: this.refresh_token,
      }).then(response => {
        if (response.data.token) {
          // jwt
          sessionStorage.user_token = response.data.token;
          sessionStorage.user_id = response.data.id;
          sessionStorage.user_name = response.data.username;
          sessionStorage.user_avatar = response.data.avatar;
          localStorage.removeItem("user_token");
          localStorage.removeItem("user_id");
          localStorage.removeItem("user_name");
          localStorage.removeItem("user_avatar");

          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'success'
          }).then(() => {
            // 
            this.$router.push("/users");
          }).catch(() => {
            // 
            this.$router.push("/");
          });
        } else {
          // QQ,
          this.is_show_page = true;
          this.openid = response.data.openid;
          this.access_token = response.data.access_token;
          this.refresh_token = response.data.refresh_token;
        }
      }).catch(error => {
        let self = this;
        self.$message({
          type: "error",
          duration: 2000,
          message: "QQ!,....",
          onClose() {
            self.$router.push("/user/login");
          }
        });
      })
    },

  }
}
</script>

