export default {
  Host: "http://api.renran.fuguang.top", // ajax
  // Host: "http://api.renran.cn:8000", // ajax
  TC_captcha: {
    // app_idDjangoapp_id
    app_id: "2060272005",
  },
  check_user_login(vm) {
    let token = localStorage.user_token || sessionStorage.user_token;
    if (!token) {
      vm.$confirm(',', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'info'
      }).then(() => {
        // 
        vm.$router.push("/user/login");
      }).catch(() => {
        // 
        vm.$router.push("/");
      });
    } else {
      vm.is_show_page = true;
      return token;
    }
  },
}



